import React, { useState, useEffect } from "react";
import BusinessItem from "./BusinessItem";
import CardItem from "./CardItem";
import "./Cards.css";

function Cards() {
      const [news, setNews] = useState([]);

      useEffect(() => {
            (async () => {
                  let api = await fetch(
                        "https://newsapi.org/v2/top-headlines?sources=bbc-news&apiKey=36995740594445f691e60d55ada5e38a"
                  );
                  let data = await api.json();
                  console.log(data.articles);
                  setNews(data.articles);
            })();
      }, []);

      return (
            <div className='cards'>
                  <h1
                        style={{
                              fontSize: 50,
                              color: "cadetblue",
                              textAlign: "center",
                        }}
                  >
                        TRENDING NEWS
                  </h1>
                  <div className='cards-container'>
                        <div className='cards-wrapper'>
                              {news.map((val) => {
                                    return (
                                          <div className='cards-items'>
                                                <CardItem
                                                      src={val.urlToImage}
                                                      text={val.content}
                                                      label={val.description}
                                                      path='/trendingnews'
                                                />
                                                <BusinessItem
                                                      src={val.urlToImage}
                                                      text={val.content}
                                                      label={val.description}
                                                      path='/businessnews'
                                                      title={val.title}
                                                />
                                          </div>
                                    );
                              })}
                        </div>
                  </div>
            </div>
      );
}

export default Cards;
